.settings fieldset {
    margin: 30px 0;
    padding: 20px;
}

.settings fieldset legend {
    font-weight: bold;
}

.settings label {
    display: inline-block;
    margin: 0 5px;
}

.settings label > img {
    cursor: pointer;
}

.settings fieldset:nth-child(1) input {
    display: block;
}

.start-btn {
    color: #fff;
    font-size: 16px;
    padding: 10px 20px;
    cursor: pointer;
    border: none;
    text-transform: uppercase;
    background-color: #ff0500;
}



